import React from 'react';
import { withRouter } from 'react-router-dom';
import { LinkContainer } from 'react-router-bootstrap';
import {
  Button,
  Glyphicon,
  Tooltip,
  OverlayTrigger,
  Table,
} from 'react-bootstrap';

const IssueRow = withRouter(
  ({ issue, location: { search }, closeIssue, index, deleteIssue }) => {
    const selectLocation = { pathname: `/issues/${issue.id}`, search };

    const editTooltip = (
      <Tooltip id="close-tooltip" placement="top">
        Edit Issue
      </Tooltip>
    );

    const closeTooltip = (
      <Tooltip id="close-tooltip" placement="top">
        Close Issue
      </Tooltip>
    );

    const deleteTooltip = (
      <Tooltip id="delete-tooltip" placement="top">
        Delete Issue
      </Tooltip>
    );

    function onClose(e) {
      e.preventDefault();
      closeIssue(index);
    }

    function onDelete(e) {
      e.preventDefault();
      deleteIssue(index);
    }

    // console.log(JSON.stringify(search));
    const tableRow = (
      <tr>
        <td>{issue.id}</td>
        <td>{issue.owner}</td>
        <td>{issue.status}</td>
        <td>{issue.title}</td>
        <td>{issue.created.toDateString()}</td>
        {/* if due date is present then convert to string else return empty string */}
        <td>{issue.due ? issue.due.toDateString() : ''}</td>
        <td>{issue.effort}</td>
        <td>
          <LinkContainer to={`/edit/${issue.id}`}>
            <OverlayTrigger delayShow={1000} overlay={editTooltip}>
              <Button bsSize="xsmall">
                <Glyphicon glyph="edit" />
              </Button>
            </OverlayTrigger>
          </LinkContainer>{' '}
          <OverlayTrigger delayShow={1000} overlay={closeTooltip}>
            <Button bsSize="xsmall" onClick={onClose}>
              <Glyphicon glyph="remove" />
            </Button>
          </OverlayTrigger>
          {'  '}
          <OverlayTrigger delayShow={1000} overlay={deleteTooltip}>
            <Button bsSize="xsmall" onClick={onDelete}>
              <Glyphicon glyph="trash" />
            </Button>
          </OverlayTrigger>
        </td>
      </tr>
    );

    return <LinkContainer to={selectLocation}>{tableRow}</LinkContainer>;
  }
);

export default function IssueTable({ issues, closeIssue, deleteIssue }) {
  // issues list passed as props from IssueList, convert into IssueRow
  const issueRows = issues.map((issue, index) => {
    return (
      <IssueRow
        key={issue.id}
        issue={issue}
        closeIssue={closeIssue}
        index={index}
        deleteIssue={deleteIssue}
      />
    );
  });

  return (
    <Table bordered condensed hover responsive>
      <thead>
        <tr>
          <th>ID</th>
          <th>Owner</th>
          <th>Status</th>
          <th>Title</th>
          <th>Created</th>
          <th>Due</th>
          <th>Effort</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        {/*  array of IssueRow */}
        {issueRows}
      </tbody>
    </Table>
  );
}
